import Section from "../Section/Section";

import styles from "./aboutUs.module.scss";

const AboutUs = () => {
  return (
    <div id="aboutUs" className={styles.aboutUsWrapper}>
      <Section>
        <div className={styles.descriptionWrapper}>
          <p className={styles.description}>
            <span className={styles.partOfText}>
              Інтернет-магазин дитячих товарів Бу-Бу - зона комфорту малюків і
              їх батьків. Наш асортимент розроблений в діапазоні "від народження
              до школи", завдяки чому кожен етап розвитку вашої дитини буде
              максимально правильним, а головне, цікавим і радісним.
            </span>
            <span className={styles.partOfText}>
              Товари для дітей - особлива категорія продукції, тому ми довіряємо
              тільки перевіреним виробникам та брендам, які давно і успішно
              представлені на світовому ринку. Магазин Бу-Бу є дистриб'ютором
              таких брендів: Anex, X-lander, Leonardo, Bebe Confort, Welldon,
              Huffy, Fun Time, Lexus Trike, Nino, Maltex, Ceba baby, X-rider,
              Play WOW, Miniland, Casato, Klups, Radir, Keenway, Adamex, Roan,
              Tako.
            </span>
            <span className={styles.more}>
              На сайті інтернет магазину Бу-Бу ви можете купити товари для дітей
              абсолютно різних категорій, в залежності від потреб: дитячі
              коляски, дитяча кімната, стільці і шезлонги, все для годування,
              гігієна та догляд, автокрісла, дитячий транспорт, іграшки, дитячий
              одяг.
            </span>
          </p>
        </div>
        <div className={styles.btnWrapper}>
          <button className={styles.btn}>Докладніше</button>
        </div>
      </Section>
    </div>
  );
};

export default AboutUs;
