import Slider from "react-slick";

import feedImg from "../../assets/images/promotionalBlock/feed.jpg";
import chairImg from "../../assets/images/promotionalBlock/chair.jpg";

import BlackTitle from "../../components/Title/BlackTitle";
import Section from "../Section/Section";
import ProductItem from "../ProductItem/ProductItem";

import styles from "./wasWatched.module.scss";
import "slick-carousel/slick/slick-theme.css";
import "slick-carousel/slick/slick.css";

const WasWatched = () => {
  const resize = () => {
    if (window.innerWidth <= 599) {
      return 2;
    } else if (window.innerWidth <= 999) {
      return 3;
    } else if (window.innerWidth <= 1289) {
      return 4;
    } else {
      return 5;
    }
  };

  const settings = {
    swipeToSlide: true,
    infinite: false,
    speed: 500,
    slidesToShow: resize(),
    slidesToScroll: 1,
  };
  return (
    <div className={styles.wasWatchedWrapper}>
      <Section>
        <div className={styles.titleWrapper}>
          <BlackTitle text="Ви переглядали" />
        </div>

        <Slider {...settings} className={styles.wasWatchedList}>
          <li>
            <ProductItem
              image={feedImg}
              name="Суміш молочна суха Nutrilon для..."
              oldPrice="350грн"
              price="300грн"
              sell="-15%"
            />
          </li>
          <li>
            <ProductItem
              image={chairImg}
              name="Автокрісло Maxi-Cosi Citi Concrete grey"
              price="4000грн"
            />
          </li>
        </Slider>
      </Section>
    </div>
  );
};

export default WasWatched;
