import Slider from "react-slick";
import { Link } from "react-router-dom";

import Section from "../Section/Section";
import BlackTitle from "../Title/BlackTitle";

import playDohImg from "../../assets/images/popularBrand/play-doh.png";
import huggiesImg from "../../assets/images/popularBrand/huggies.png";
import hippImg from "../../assets/images/popularBrand/hipp.png";
import pampersImg from "../../assets/images/popularBrand/pampers.png";
import anexImg from "../../assets/images/popularBrand/anex.png";
import legoImg from "../../assets/images/popularBrand/lego.png";
import bebivitaImg from "../../assets/images/popularBrand/bebivita.png";

import styles from "./popularBrand.module.scss";
import "slick-carousel/slick/slick-theme.css";
import "slick-carousel/slick/slick.css";

const PopularBrand = () => {
  const resize = () => {
    if (window.innerWidth <= 479) {
      return 3;
    } else if (window.innerWidth <= 999) {
      return 4;
    } else if (window.innerWidth <= 1289) {
      return 5;
    } else {
      return 7;
    }
  };

  const settings = {
    lazyLoad: true,
    swipeToSlide: true,
    infinite: true,
    speed: 500,
    slidesToShow: resize(),
    slidesToScroll: 1,
  };
  return (
    <div className={styles.popularBrandWrapper}>
      <Section>
        <div className={styles.titleWrapper}>
          <BlackTitle text="Популярні бренди" />
        </div>

        <Slider {...settings}>
          <li>
            <Link className={styles.link}>
              <img src={playDohImg} alt="" />
            </Link>
          </li>
          <li>
            <Link className={styles.link}>
              <img src={huggiesImg} alt="" />
            </Link>
          </li>
          <li>
            <Link className={styles.link}>
              <img src={hippImg} alt="" />
            </Link>
          </li>
          <li>
            <Link className={styles.link}>
              <img src={pampersImg} alt="" />
            </Link>
          </li>
          <li>
            <Link className={styles.link}>
              <img src={anexImg} alt="" />
            </Link>
          </li>
          <li>
            <Link className={styles.link}>
              <img src={legoImg} alt="" />
            </Link>
          </li>
          <li>
            <Link className={styles.link}>
              <img src={bebivitaImg} alt="" />
            </Link>
          </li>
        </Slider>
      </Section>
    </div>
  );
};

export default PopularBrand;
