import { NavLink } from "react-router-dom";

import Section from "../Section/Section";
import BlackTitle from "../Title/BlackTitle";

import carriageImg from "../../assets/images/popularCatagories/carriage.png";
import bedImg from "../../assets/images/popularCatagories/bed.png";
import potImg from "../../assets/images/popularCatagories/pot.png";
import puzzleImg from "../../assets/images/popularCatagories/puzzles.png";
import bathImg from "../../assets/images/popularCatagories/bath.png";
import roomImg from "../../assets/images/popularCatagories/room.png";

import styles from "./popularCategories.module.scss";

const PopularCategories = () => {
  return (
    <div className={styles.popularCategoriesWrapper}>
      <Section>
        <div className={styles.titleWrapper}>
          <BlackTitle text="Популярні Категорії" />
        </div>

        <div className={styles.catagoriesList}>
          <div className={styles.upContainer}>
            <NavLink
              style={{
                backgroundColor: "rgba(106, 210, 69, 1)",
              }}
              className={` ${styles.link} ${styles.biggerLink}`}
            >
              <img
                className={`${styles.img} ${styles.largeImg}`}
                src={carriageImg}
                alt=""
              />
              <p
                style={{
                  backgroundColor: "rgba(84, 173, 51, 1)",
                }}
                className={styles.text}
              >
                Коляски 2в1
              </p>
            </NavLink>

            <div className={styles.lessLinkWrapper}>
              <NavLink
                style={{
                  backgroundColor: "rgba(249, 156, 17, 1)",
                }}
                className={`${styles.lessLink} ${styles.link}`}
              >
                <img className={styles.img} src={roomImg} alt="" />
                <p
                  style={{
                    backgroundColor: "rgba(219, 131, 0, 1)",
                  }}
                  className={styles.text}
                >
                  Дитячі ліжечка
                </p>
              </NavLink>

              <NavLink
                style={{
                  backgroundColor: "rgba(45, 234, 228, 1)",
                }}
                className={`${styles.lessLink} ${styles.link}`}
              >
                <img className={styles.img} src={potImg} alt="" />
                <p
                  style={{
                    backgroundColor: "rgba(31, 192, 187, 1)",
                  }}
                  className={styles.text}
                >
                  Горщики
                </p>
              </NavLink>
            </div>
          </div>

          <div className={styles.downContainer}>
            <div className={styles.lessLinkWrapper}>
              <NavLink
                style={{
                  backgroundColor: "rgba(216, 69, 69, 1)",
                }}
                className={`${styles.lessLink} ${styles.link}`}
              >
                <img className={styles.img} src={puzzleImg} alt="" />
                <p
                  style={{
                    backgroundColor: "rgba(164, 58, 58, 1)",
                  }}
                  className={styles.text}
                >
                  Пазли
                </p>
              </NavLink>

              <NavLink
                style={{
                  backgroundColor: "rgba(162, 79, 226, 1)",
                }}
                className={`${styles.lessLink} ${styles.link}`}
              >
                <img className={styles.img} src={bathImg} alt="" />
                <p
                  style={{
                    backgroundColor: "rgba(120, 52, 173, 1)",
                  }}
                  className={styles.text}
                >
                  Ванночки
                </p>
              </NavLink>
            </div>

            <NavLink
              style={{
                backgroundColor: "rgba(79, 138, 226, 1)",
              }}
              className={`${styles.link} ${styles.biggerLink} `}
            >
              <img className={styles.img} src={bedImg} alt="" />
              <p
                style={{
                  backgroundColor: "rgba(56, 104, 178, 1)",
                }}
                className={styles.text}
              >
                Дитяча постільна білизна
              </p>
            </NavLink>
          </div>
        </div>
      </Section>
    </div>
  );
};

export default PopularCategories;
