import Slider from "react-slick";

import Section from "../Section/Section";
import BlackTitle from "../Title/BlackTitle";
import ProductItem from "../ProductItem/ProductItem";

import { ReactComponent as SaleIcon } from "../../assets/images/promotionalBlock/saleIcon.svg";

import carriageImg from "../../assets/images/promotionalBlock/carriage.jpg";
import toyImg from "../../assets/images/promotionalBlock/toy.jpg";
import buttleImg from "../../assets/images/promotionalBlock/buttle.jpg";
import feedImg from "../../assets/images/promotionalBlock/feed.jpg";
import chairImg from "../../assets/images/promotionalBlock/chair.jpg";

import styles from "./promotional.module.scss";
import "slick-carousel/slick/slick-theme.css";
import "slick-carousel/slick/slick.css";

const Promotional = () => {
  const resize = () => {
    if (window.innerWidth <= 599) {
      return 2;
    } else if (window.innerWidth <= 999) {
      return 3;
    } else if (window.innerWidth <= 1289) {
      return 4;
    } else {
      return 5;
    }
  };

  const settings = {
    lazyLoad: true,
    swipeToSlide: true,
    infinite: true,
    speed: 500,
    slidesToShow: resize(),
    slidesToScroll: 1,
  };

  return (
    <div className={styles.promotionalWrapper}>
      <Section>
        <div className={styles.titleWrapper}>
          <BlackTitle text="Акція!" />
          <SaleIcon />
        </div>
        <Slider {...settings}>
          <li>
            <ProductItem
              image={carriageImg}
              name="Коляска 2в1 Anex M/Type Dune mt-01Q"
              oldPrice="6000грн"
              price="5000грн"
              sell="-10%"
            />
          </li>
          <li>
            <ProductItem
              image={chairImg}
              name="Автокрісло Maxi-Cosi Citi Concrete grey"
              price="4000грн"
            />
          </li>
          <li>
            <ProductItem
              image={toyImg}
              name="Іграшка-головоломка Same Toy IQ Ball Cube"
              oldPrice="300грн"
              price="200грн"
              sell="-30%"
            />
          </li>
          <li>
            <ProductItem
              image={buttleImg}
              name="Пляшечка 130 мл"
              oldPrice="150грн"
              price="100грн"
              sell="-30%"
            />
          </li>
          <li>
            <ProductItem
              image={feedImg}
              name="Суміш молочна суха Nutrilon для..."
              oldPrice="350грн"
              price="300грн"
              sell="-15%"
            />
          </li>

          <li>
            <ProductItem
              image={carriageImg}
              name="Коляска 2в1 Anex M/Type Dune mt-01Q"
              oldPrice="6000грн"
              price="5000грн"
            />
          </li>
          <li>
            <ProductItem
              image={toyImg}
              name="Іграшка-головоломка Same Toy IQ Ball Cube"
              oldPrice="300грн"
              price="200грн"
            />
          </li>
        </Slider>
      </Section>
    </div>
  );
};

export default Promotional;
